.portfolio {
	padding: 30px 15px;
	padding-top: 0;
	h1 {
		line-height: 1;
		color: #fff;
		font-weight: 300;
		text-align: center;
		margin-bottom: 30px;
	}
	&__wrap {
		display: grid;
		grid-template: auto/ 1fr 1fr;
		gap: 30px;
		&-column {
			display: flex;
			flex-direction: column;
			gap: 15px;
		}
	}
	&__item {
		padding: 20px 15px;
		@include glass;
		display: flex;
		flex-direction: column;
		color: white;
		a {
			color: white;
			display: flex;
			flex-shrink: 0;
			transition: 0.3s;
			div {
				flex-shrink: 0;
				@include glass;
				width: 60px;
				height: 60px;
				display: flex;
				justify-content: center;
				font-size: 40px;
				line-height: 55px;
				font-weight: 300;
			}
			p {
				margin-top: 0;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				width: 100%;
				span {
					font-size: 12px;
				}
			}
			&:hover {
				text-decoration: none;
				text-shadow: #fff 1px 0 10px ;
			}
		}
		p {
			font-size: 20px;
			margin-top: 20px;
			margin-bottom: 0;
		}
	}
}